<template>
    <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="50%"
        :before-close="cancel">
        <div class="row-list">
            <span class="name">第三方订单编号：</span>
            <el-input v-model="data.orderNum" :disabled="true"/>
        </div>
        <div class="row-list">
            <span class="name">收货人姓名：</span>
            <el-input v-model="data.userName" :disabled="true"/>
        </div>
        <div class="row-list">
            <span class="name">收货人手机号：</span>
            <el-input v-model="data.mobile" />
        </div>
        <div class="row-list">
            <span class="name">收货人地址：</span>
            <el-input v-model="data.address" />
        </div>
        <div class="row-list">
            <span class="name">商品名：</span>
            <el-input v-model="data.excelGoodsName" :disabled="true"/>
        </div>
        <div class="row-list">
            <span class="name">买家账号：</span>
            <el-input v-model="data.buyAccount" :disabled="true"/>
        </div>

        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="confirm">确 定</el-button>
        </span>
    </el-dialog>
</template>

<script>
export default {
    props:['data', 'title'],
    data() {
        return {
            dialogVisible: true,
        };
    },
    methods: {
        cancel(){
            this.$emit('cancel');
        },
        confirm(){
            this.$emit('confirm', this.data);
        }
    }
}
</script>

<style lang="less" scoped>
    .row-list{
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        .name{
            width: 150px;
        }
        &:last-child{
            margin: 0;
        }
    }
</style>



