        checkInfoList() {
            if (this.buyList.length == 0) {
                this.$message({
                    message: "请最少选择一条数据",
                    type: "warning"
                });
                return flase;
            }
            let errList = [];
            this.tableData = this.tableData.map((item, index) => {
