        add(item, num) {
            let that = this;
            let isHave = that.shopcar.find(element => element.id == item.id);
            if (isHave) {
                for (var i = 0; i < that.shopcar.length; i++) {
                    if (item.id == that.shopcar[i].id) {
                        that.$set(
                            that.shopcar[i],
                            "num",
                            Number(num) + Number(that.shopcar[i].num)
                        );
                        that.$set(
                            that.shopcar[i],
                            "total",
                            (parseFloat(item.price * 100).toFixed(2) *
                                that.shopcar[i].num) /
                                10000
                        );
                    }
                }
            } else {
                that.$set(item, "num", num);
                that.$set(
                    item,
                    "total",
                    (parseFloat(item.price * 100).toFixed(2) * item.num) / 10000
                );
                that.shopcar.push(item);
            }

            that.changePrize();
        },
        del(index) {
