        calculateMoney(flag) {
            const list = [];
            console.log(this.shopcar);
            this.shopcar = this.shopcar.length
                ? this.shopcar
                : JSON.parse(sessionStorage.getItem("shopcar")) || [];
            this.shopcar.forEach((item, index) => {
                list.push({
                    goodsId: item.id,
                    goodsCount: item.num,
                    name: item.name,
                    price: item.price,
                    num: item.num,
                    weight: item.weight,
                    images: item.images
                });
            });
            this.trueList = [];
            this.buyList.forEach((item, index) => {
                if (flag == "all") {
                    this.buyList[index].forEach((item, index2) => {
                        this.buyList[index][index2].singleOrderDtoList = list;
                    });
                } else if (flag == index) {
                    this.buyList[flag].forEach((item, index2) => {
                        this.buyList[index][index2].singleOrderDtoList = list;
                    });
                }
                this.trueList = [...this.trueList, ...this.buyList[index]];
            });
            console.log(this.trueList);
            this.checkMoney(this.trueList);
        },
        caigou() {
            const list = [];
            this.shopcar.forEach((item, index) => {
                list.push({
                    goodsId: item.id,
                    goodsCount: item.num,
                    name: item.name,
                    price: item.price,
                    num: item.num,
                    weight: item.weight,
                    images: item.images
                });
            });

            this.buyList[this.arryindex].forEach((item, index) => {
                this.buyList[this.arryindex][index].singleOrderDtoList = list;
            });
            console.log(that.buyList);
            this.shopcar = [];
            this.trueList[this.arryindex].singleOrderDtoList = list;
            //that.trueList=that.buyList.flat(2)
            console.log(this.trueList);
