                    if (response.data.code == 200) {
                        console.log("caigou1");

                        that.paytotal = response.data.data;
                    }
                });
        },
        checkMoney(params) {
            this.axios
                .post("/giftmall/task/checkMoney", {
                    trueList: params
                })
                .then(response => {
                    if (response.data.code == 200) {
                        this.paytotal = response.data.data;
                    }
                });
        },
        caigou2() {
            this.shopcar = this.shopcar.length
                ? this.shopcar
                : JSON.parse(sessionStorage.getItem("shopcar")) || [];
            for (var i = 0; i < this.trueList.length; i++) {
                let newarry = [];
                for (var j = 0; j < this.shopcar.length; j++) {
                    newarry.push({
                        goodsId: this.shopcar[j].id,
                        goodsCount: this.shopcar[j].num,
                        name: this.shopcar[j].name,
                        images: this.shopcar[j].images,
                        weight: this.shopcar[j].weight,
                        num: this.shopcar[j].num,
                        price: this.shopcar[j].price
                    });
                }

                this.trueList[i] = Object.assign(this.trueList[i], {
                    singleOrderDtoList: newarry
                });
            }
            this.checkMoney(this.trueList);
        },
        changePrize() {
            let that = this;
            that.zhongliang = (
                parseFloat(
                    that.shopcar.reduce(
                        (num, next) => next.weight * next.num + num,
                        0
                    )
                ) / 1000
            ).toFixed(2);
            that.productPrize = that.shopcar
                .reduce((num, next) => next.total + num, 0)
                .toFixed(2);
            if (that.zhongliang == 0) {
                that.yunfei = 0;
            } else if (that.zhongliang <= 0.5) {
                that.yunfei = (that.priceForm.nearPrice1 / 100).toFixed(2);
            } else if (that.zhongliang > 0.5 && that.zhongliang <= 1) {
                that.yunfei = (that.priceForm.nearPrice2 / 100).toFixed(2);
            } else if (that.zhongliang > 1) {
                that.yunfei = (
                    (Math.ceil(that.zhongliang - 1) *
                        that.priceForm.nearPrice3) /
                        100 +
                    that.priceForm.nearPrice2 / 100
                ).toFixed(2);
            }
            that.totalPrize = (
                parseFloat(that.productPrize) + parseFloat(that.yunfei)
            ).toFixed(2);
        },
        changeNum(id, val) {
            for (var i = 0; i < this.shopcar.length; i++) {
                if (id == this.shopcar[i].id) {
                    this.$set(
                        this.shopcar[i],
                        "total",
                        (parseFloat(this.shopcar[i].price * 100).toFixed(2) *
                            val) /
                            10000
                    );
                    this.changePrize();
                }
            }
